.trade {
    display: block;
    /* scroll-snap-align: center; */
    max-width: 1100px;
    margin: 0 auto;
    justify-content: center;
    text-align: center;
    padding: 40px 10px;
}
.trade h1 {
    font-size: 38px;
    padding: 10px 0;
    font-weight: 700;
}
.trade .advantages {
    display: flex;
    max-width: 1100px;
    padding-top: 40px;
    justify-content: space-between;
    /* border: 2px solid yellow; */
}
.trade .advantages .essentials {
    display: flex;
    width: 31.67%;
    /* border: 2px solid red; */
    flex-direction: column;
    /* flex-basis: 250px; */
    border-radius: 18px;
    text-align: left;
}
.trade .advantages .essentials h2 {
    font-size: 22px;
    font-weight: 700;
    padding: 10px 0;
}
.trade .advantages .essentials p {
    color:#C4C4C4;
    font-size: 16px;
    line-height: 23px;
}

@media (max-width: 750px) {
    .trade {
        margin-top: 0;
        gap: 5rem;
        flex-direction: column;
    }
    .trade h1 {
        font-size: 25.9px;
        font-weight: 700;
    }
    .trade .advantages {
        flex-direction: column;
        gap: 30px;

    }
    .trade .advantages .essentials {
        width: 70%;
        margin: 0 auto;
    }
}
@media (max-width: 555px) {
    .trade .advantages {
        flex-direction: column;
        gap: 10px;

    }
}