.hero-wrapper {
    /* scroll-snap-align: center; */
    padding-top: 20px;
    height: calc(100vh - 50px);
    margin: 0 auto;
    overflow: hidden;
    /* border: 2px solid red; */
    background: linear-gradient(120.88deg, #11131D 31.29%, #2D3786 96.35%);
}
.hero-wrapper .hero {
    display: flex;
    height: calc(100vh - 50px);
    max-width: 1100px;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}
.hero .heroleft {
    width: 40%;
    line-height: 40px;
    padding: 20px 0;
    /* border: 2px solid red; */
}
.hero .heroleft h1 {
    font-size: 58px;
    font-weight: 800;
    line-height: 70px;
    
}
.hero .heroleft h1 span {
    background-image: linear-gradient(116.95deg, #06AEE2 40%, #FB3BED 69.48%),
    linear-gradient(0deg, #FFFFFF, #FFFFFF);
    -webkit-background-clip: text;
    color: transparent;

}
.hero .heroleft h2 {
    font-size: 50px;
    font-weight: 350;
    line-height: 70px;
}
.hero .heroleft p {
    line-height: 20px;
    font-weight: 400;
    color: #C4C4C4;
    padding: 30px 0;
}
.hero .heroleft .icons {
    display: flex;
    justify-content: space-between;
    max-width: 500px;
    height: 56px;
}
.hero .heroleft .icons .item {
    display: flex;
    flex-direction: column;
}
.hero .heroleft .icons .item img {
    height: 26px;
}
.hero .heroleft .frame {
    display: flex;
    max-width: 700px;
    height: 50px;
}
.hero button {
    padding: 10px 20px;
    color: whitesmoke;
    font-weight: 700;
    margin-top: 30px;
    background: #5B3FF2;
    border-radius: 8px;
    border: transparent;
    cursor: pointer;
}
.hero .heroright {
    /* display: flex; */
    position: relative;
    /* overflow: hidden; */
    top: 0;
    left: 60px;
    margin: 0 auto;
}
.hero .heroright>:nth-child(1) {
    position: relative;
    top: 0px;
    transform: scale(0.67);
}
.hero .heroright>:nth-child(2) {
    bottom: 170px;
    position: absolute;
    z-index: 2;
    left: -30px;
    /* left: -150px; */
    height: 140px;
}
@media (max-width: 1100px) {
    .hero .heroleft {
        padding: 20px;
    }
    .hero .heroleft h1  {
        font-size: 40px;
        line-height: 50px;
    }
    .hero .heroleft h2  {
        /* font-size: 40px; */
        line-height: 60px;
    }
    .hero .heroleft p .responsive {
        display: none;
    }
}


@media (max-width: 925px) {
    .hero .heroleft h1  {
        font-size: 40px;
        font-weight: 700;
        /* line-height: 60px; */
    }
    .hero .heroright>:nth-child(1) {
        /* top: -150px; */
        height: inherit;
    }
}

@media (max-width: 830px) {
    .hero-wrapper {
        display: flex;
        /* height: fit-content; */
        flex-direction: column;
        height: max-content;
    }
    .hero-wrapper .hero {
        display: flex;
        overflow: visible;
        height: max-content;
        flex-direction: column;
        gap: 25px;
        /* border: 2px solid blue; */
        margin: 0 auto;
        
        justify-content: center;
    }
    .hero .heroleft {
        line-height: 20px;
        height: 60%;
        width: 100%;
        padding: 40px;
        /* max-height: 90vh; */
    }
    .hero .heroleft h1 {
        font-size: 38px;
        font-weight: 700;
        line-height: 50px;
        
    }
    .hero .heroleft h2 {
        font-size: 38px;
        font-weight: 350;
        line-height: 50px;
    }
    .hero .heroleft p {
        line-height: 20px;
        font-weight: 400;
        color: #C4C4C4;
        padding: 20px 0;
    }
    .hero .heroleft .icons .item {
        gap: 15px;
    }

    .hero .heroright {
        margin: 0 auto;
        left: 0;
    }
    .hero .heroright>:nth-child(1) {
        /* top: -150px; */
        height: inherit;
    }
    .hero .heroright>:nth-child(2) {
        left: 10px;
        width: 140px;
    }
}
@media (max-width: 555px) {
    .hero .heroleft {
        padding: 10px;
        text-align: left;
    }
    .hero .heroright {
        height: 65vh;
    }
    .hero .heroright>:nth-child(1) {
        /* left: -100px; */
        top: 10px;
        margin: 0;
        height: inherit;
        width: 100%;
        transform: scale(0.95);
        /* width: auto; */
    }
    .hero .heroright>:nth-child(2) {
        display: none;
    }
    
}
@media (max-width: 330px) {
    .hero button {
        transform: scale(0.7);
    }
    .hero .heroleft {
        padding: 10px;
        text-align: left;
    }
    .hero .heroleft h1  {
        font-size: 30px;
        font-weight: 700;
        /* line-height: 60px; */
    }
}