.coins {
    max-width: 1100px;
    /* scroll-snap-align: center; */
    display: flex;
    flex-direction: column;
    margin: auto;
    padding:  40px 20px;
    /* gap: 30px; */
}
.coins .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.coins .heading h1 {
    font-size: 38px;
    font-weight: 700;
}

.coins .heading .options>:nth-child(1) {
    padding: 10px;
    background: #5B3FF2;
    margin-right: 10px;
    font-weight: 700;
    border: transparent;
    border-radius: 45px;    
    cursor: pointer;
    color: white;

}
.coins .heading .options>:nth-child(2) {
    padding: 10px;
    background: transparent;
    margin-right: 10px;
    border: transparent;
    border-radius: 45px;    
    cursor: pointer;
    color: white;

}
.coins .coin-body {
    max-width: 1100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
    border-bottom: 1px solid  #43454F;
}
.coins .coin-body .assets {
    display: flex;
    padding: 0 20px;
    gap: 50px;
}
.coin-item {
    display: flex;
    flex-direction: column;
}
.coin-item .img-symbol {
    border-bottom: 1px solid  #43454F;
    padding: 25px 0;
    align-items: center;
    display: flex;
    justify-content: space-between;
}
.coin-item .img-symbol:hover {
    transform: scale(1.05);
    transition: 0.3s ease-in-out;
}
.coin-item .img-symbol .image {
    gap: 20px;
    display: flex;
}
.coin-item .img-symbol .image .name p {
    font-weight: 700;
}
.coin-item .img-symbol .image .name h6 {
    font-weight: 400;
}
.coin-item .img-symbol button {
    padding: 10px;
    background: #5B3FF2;
    border: transparent;
    border-radius: 45px;    
    font-weight: 700;
    cursor: pointer;
    color: white;
}

@media (max-width: 750px) {
    .coins .heading h1 {
        font-size: 25px;
        font-weight: 700;
    }
    .hide-mobile {
        display: none;
    }
    .coins .heading .options>:nth-child(2) {
        display: none;
    
    }
    .coin-item .img-symbol {
        display: flex;
        justify-content: space-between;
        /* border: 2px solid red; */
    }
    .coins .coin-body .assets {
        display: flex;
        padding: 0;
        gap: 10px;
    }
    .coin-item .img-symbol .image .name {
        margin-right: 40px;
    }
}