.footer {
    display: flex;
    /* scroll-snap-align: center; */
    height: 100vh;
    flex-direction: column;
    text-align: center;
    max-width: 1100px;
    /* border: 2px solid red; */
    margin: 0 auto;
    padding: 40px 10px;
}
.footer .trading {
    display: flex;
    flex-direction: column;
    background: linear-gradient(102.43deg, #693FED 30.9%, #3845EC 54.35%, #0BA4E3 86.08%);
    border-radius: 28px;
    gap: 20px;
    padding: 10px;
    max-width: 1100px;
    height: 65%;
    align-items: center;
    justify-content: center;
    /* border: 2px solid red; */
}
.footer .trading h1 {
    font-weight: 900;
    font-size: 30px;
}
.footer .trading button {
    padding: 10px 20px;
    color: white;
    border-color: transparent;
    font-weight: 700;
    cursor: pointer;
    background: #2470E8;
    border-radius: 8px;

}
.footer .footer-block {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 40px;
    /* border: 2px solid red; */
}
.footer .footer-block .resources{
    display: flex;
    flex-direction: column;
    text-align: left;
    color: #C4C4C4;
    gap: 20px;
}
.footer .footer-block .resources h2 {
        color: white;
}
.footer .footer-block .resources .email {
    width: 100%;
}
.footer .footer-block .resources .email input {
    padding: 10px;
    width: 65%;
    background: #1B1D27;
    color: white;
    border-color: transparent;

}
.footer .footer-block .resources .email button {
    width: 35%;
    padding: 10px 5px;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    background: #5B3FF2;
    border-color: transparent;

}
.footer .footer-block .resources li {
    list-style: none;
}
.footer .footer-block .trade-next {
    display: flex;
    flex-direction: column;
    text-align: left;
    /* border: 2px solid green; */
    gap: 15px;
    width: 30%;
}
.footer .footer-block .trade-next p {
    color: #C4C4C4;
}
.footer .footer-block .trade-next .header {
    display: flex;
    gap: 10px;
    align-items: center;
}
.footer .footer-block .trade-next .header  h2 {
    font-size: 30px;
}
.footer .footer-block .trade-next .social-media {
    display: flex;
    align-items: center;
    gap: 10px;
}
.footer .footer-block .trade-next .social-media img {
    height: 40px;
}

@media (max-width: 750px) {
    .footer {
        height: fit-content;
        width: 100%;
    }
    .footer .trading p .responsive {
        display: none;
    }
    .footer .footer-block {
        display: flex;
        flex-direction: column;
    }
    .footer .footer-block .resources {
        gap: 10px;
    }
    .footer .footer-block .resources h2 {
        margin-top: 10px;
}
    .footer .footer-block .trade-next {
        width: 100%;
    }
    
}