.steps {
    display: flex;
    flex-direction: column;
    /* scroll-snap-align: center; */
    max-width: 1100px;
    /* height: 100vh; */
    margin: 0 auto;
    padding: 40px 10px;
    text-align: left;
    /* border: 2px solid red; */
}
.steps .blockOne h1 {
    font-size: 38px;
    font-weight: 700;
}
.steps .blockOne p {
    padding: 20px 0;
    font-size: 22px;
    line-height: 30px;
    color:#C4C4C4;
}
.steps .blockTwo {
    display: flex;
    height: 100%;
    width: 100%;
    gap: 20px;
    /* border: 2px solid red; */
    justify-content: space-between;
}
.steps .blockTwo .unitOne {
    display: flex;
    gap: 30px;
    width: 50%;
}
.steps .blockTwo .unitOne .rank {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    /* border: 2px solid red; */
    width: 20%;
}
.steps .blockTwo .unitOne .rank span {
    padding: 10px;
    font-size: large;
    font-weight: 600;
    border: 1px solid #21232D;
    border-radius: 100%;
}
.steps .blockTwo .unitOne .rank>:nth-child(1) {
    padding: 10px;
    background: #5B3FF2;
    border: 1px solid #21232D;
}

.steps .blockTwo .unitOne .number {
    display: flex;
    gap: 20px;
    flex-direction: column;
    justify-content: space-between;
}
.steps .blockTwo .unitOne .step {
    display: flex;
    flex-direction: column;
    background: #13151F;
    padding: 10px;
    width: 100%;
}
.steps .blockTwo .unitOne .step h3 {
    font-size: 20px;
    font-weight: 700;
}
.steps .blockTwo .unitOne .step p {
    color:#C4C4C4;
    font-size: 16px;
}
.steps .blockTwo .unitTwo {
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.steps .blockTwo .block {
    display: flex;
    align-items: center;
    padding: 20px;
    border-radius: 16px;
    /* border: 2px solid green; */
    width: 100%;
    /* height: 30%; */
    background: #252731;
}
.steps .blockTwo .block img {
    height: 64px;
}
.steps .blockTwo .block .blocks {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    padding: 0 20px;
    /* border: 2px solid blue; */
    flex-direction: column;
}
.steps .blockTwo .block .blocks .one {
    background: #181A24;
    height: 17px;
    width: 80%;

}
.steps .blockTwo .block .blocks .two {
    background: #181A24;
    height: 17px;
    width: 60%;

}

@media (max-width: 750px) {
    .steps {
        height: fit-content;
    }
    .steps .blockOne {
        /* border: 2px solid orange; */
        height: 20%;
    }
    .steps .blockOne h1 {
        font-size: 25.9px;
    }
    .steps .blockOne p {
        line-height: 25px;
        padding: 10px 0;
    }
    .steps .blockOne p .responsive {
        display: none;
    }
    .steps .blockTwo {
        display: flex;
        /* border: 3px solid blue; */
        width: 100%;
        height: 80%;
        padding: 10px;
        flex-direction: column;
        align-items: center;
        /* text-align: center; */
    }
    .steps .blockTwo .unitOne{
        height: 40%;
        width: 100%;
    }
    .steps .blockTwo .unitTwo{
        display: flex;
        flex-direction: column;
        width: 80%;
        gap: 20px;
        /* align-items: end; */
    }
    .steps .blockTwo .unitTwo .block img {
        height: 50px;
        /* max-width: 50%; */
    }
}