.nav-wrapper {
    background: linear-gradient(120.88deg, #11131D 31.29%, #2D3786 96.35%);
    /* scroll-snap-align: center; */
}
.nav-wrapper .navbar {
    display: flex;
    justify-content: space-between;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 10px;
    height: 50px;
    align-items: center;
    /* border: 2px solid white; */
}
.navbar .l-navbar {
    /* border: 1px solid black; */
    width: 500px;
    justify-content: space-between;
    display: flex;
    align-items: center;
}
.navbar .l-navbar .logo {
    display: flex;
    gap: 10px;
    align-items: center;
}
.navbar .l-navbar .logo h1 {
    font-size: 26px;
    font-weight: 700;
    line-height: 18.78px;
}
.navbar .l-navbar ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 300px;

}
.navbar .l-navbar ul a {
    cursor: pointer;
}
.navbar .l-navbar ul a:hover {
    transform: scale(1.2);
    transition: 0.3s ease-in-out;
}

.navbar .r-navbar {
    max-width: 289px;
    display: flex;
    gap: 30px;
    align-items: center;
}
.navbar .r-navbar ul {
    display: flex;
    align-items: center;
}
.navbar .r-navbar ul:hover {
    transform: scale(1.2);
    transition: 0.3s ease-in-out;
}
.navbar .r-navbar ul a {
    cursor: pointer;
}
.navbar .r-navbar button {
    padding: 10px 20px;
    color: whitesmoke;
    background: #5B3FF2;
    font-weight: 700;
    /* height: 50px; */
    border-radius: 8px;
    border: transparent;
    cursor: pointer;
}
.navbar .r-navbar button:hover {
    transform: scale(0.95);
    transition: .3s ease-in-out;
}

@media (max-width: 830px) {
    .navbar .l-navbar ul {
        display: none;
    }
    .navbar .r-navbar button {
        transform: scale(0.8);
    }
}
@media (max-width: 555px) {
    .navbar .r-navbar ul {
        display: none;
    }
    .navbar .r-navbar button  {
        padding: 5px 30px;
    }
}
@media (max-width: 330px) {
    .navbar .r-navbar button {
        transform: scale(0.7);
    }
}