.coin-wrapper {
    /* border: 2px solid red; */
    display: flex;
    /* scroll-snap-align: center; */
    margin: 70px auto;
    width: 100%;
    padding: 0 10px;
    justify-content: space-between;
    max-width: 1100px;
}
.coin-wrapper .coin {
    display: flex;
    justify-content: space-between;
    gap: 8px;
    /* border: 2px solid blue; */
    background: #1B1D27;
    border-radius: 18px;
    overflow: hidden;
    width: 32%;
    /* flex-basis: 360px; */
}
.coin-wrapper .coin .blocks {
    /* border: 2px solid green; */
    width: 70%;
}
.coin-wrapper .coin .gridTwo {
    display: flex;
    flex-direction: column;
    /* width: 10%; */
    justify-content: space-between;
    /* border: 2px solid red; */
}
.coin-wrapper .coin .coin-image {
    display: flex;
    justify-content: center;
    width: 20%;
    /* border: 2px solid yellow; */
    flex-direction: column;
}
.coin-wrapper .coin .coin-image>:nth-child(1) {
    /* border: 2px solid yellow; */
    position: relative;
    top: 10px;
    left: 0;
    width: 40px;
}
.coin-wrapper .coin .coin-image>:nth-child(2) {
    position: relative;
    bottom: 40px;
    right: 40px;
    /* border: 2px solid yellow; */
    width: 85px;
}
.coin-wrapper .coin .blocks {
    display: flex;
    padding: 10px;
    flex-direction: column;
    line-height: 35px;
}
.coin-wrapper .coin .blocks h1 {
    font-weight: 500;
}
.coin-wrapper .coin .blocks h3 {
    font-weight: 400;
}
.coin-wrapper .coin .blocks button {
    background-color: #06AFE2;
    border-color: transparent;
    cursor: pointer;
    font-weight: 600;
    width: 70%;
    border-radius: 25px;
}
.coin-wrapper .coin .blocks .grid {
    margin-top: 30px;
    display: flex;
    /* border: 2px solid red; */
    gap: 20px;
    /* justify-content: space-between; */
}
.coin-wrapper .coin .blocks .grid img {
    height: 25px;
}
.coin-wrapper .coin .blocks .firstBlock {
    display: flex;
    gap: 7px;
    align-items: center;
}
.coin-wrapper .coin .blocks .firstBlock img {
    height: 15px;
}
.coin-wrapper .coin .blocks .firstBlock h6 {
    font-weight: 700;
    font-size: 16px;
}

@media (max-width: 877px) {
    .coin-wrapper {
        display: flex;
        width: 100%;
        flex-direction: column;
        gap: 20px;
        justify-content: center;
    }
    .coin-wrapper .coin {
        width: 70%;
        margin: 0 auto;
    }
    
    .coin-wrapper .coin .blocks .firstBlock img {
        height: 25px;
    }
    .coin-wrapper .coin .coin-image>:nth-child(1) {
        width: 60px;
    }
    .coin-wrapper .coin .coin-image>:nth-child(2) {
        width: 105px;
    }
}
@media (max-width: 555px) {
    .coin-wrapper .coin .coin-image>:nth-child(1) {
        top: 30px;
        width: 30px;
    }
    .coin-wrapper .coin .coin-image>:nth-child(2) {
        bottom: 0;
        width: 65px;
    }
}
@media (max-width: 330px) {
    .coin-wrapper .coin .coin-image {
        display: none;
    }
    .coin-wrapper .coin .blocks .firstBlock h6 {
        display: none;
    }
}